<!--https://v3.vuejs.org/guide/forms.html#select-->

<template id="">
	<select v-model="selected">
		<option v-for="option in options" :value="option.value" v-bind:key="option.value">
			{{ option.text }}
		</option>
	</select>
</template>

<script>
export default {
	data() {
		return {
			selected: "",
		};
	},
	props : ["options"]
}
</script>
